:root {
  --Pale-Violet: #b54cff;
  --Moderate-Violet: #bc66f5;
  --Desaturated-Dark-Violet: #6e5d7e;
  --Grayish-Blue: #c6cacd;
  --Very-Dark-Desaturated: #3e2753;
  --Dark-Grayish-Violet: #a39daa;
  --Light-Magenta: #e942ff;
  --Light-Violet: #8838ff;
  --White: #fff;
  --Light-Grayish-Violet: #f5f3f7;
  --Very-Dark-Desaturated-Violet: #3e2753;
  --Very-Light-Magenta: #e570ff;
  --input-border-radius: 30px;
}

html {
  font-family: Rubik;
  font-size: 62.5%;
}

body {
  height: 100vh;
  font-size: 16px;
  display: flex;
}

.phone-container {
  background: linear-gradient(180deg, var(--Light-Magenta) -40%, var(--Light-Violet) 100%);
  border-radius: 0 0 40% 59%;
  width: 50rem;
  height: 85%;
  position: relative;
  right: 140px;
}

.phone {
  box-sizing: border-box;
  background-color: #f5f3f8;
  border: 10px solid #fff;
  border-radius: 3.9rem;
  width: 32rem;
  height: 65rem;
  position: relative;
  top: 3px;
  left: 390px;
  transform: scale(.6);
  box-shadow: -2px 2px 69px 6px #0000004d;
}

.white-container {
  background-color: #fff;
  border-radius: 0 0 1.8rem 1.8rem;
  width: 15.9rem;
  height: 2.3rem;
  margin-left: 6.7rem;
  position: absolute;
}

.phone-header {
  background: linear-gradient(90deg, var(--Light-Violet) -20%, var(--Light-Magenta) 100%);
  border-radius: 2.8rem 2.5rem 1rem 1rem;
  align-items: center;
  width: 100%;
  height: 13%;
  padding-top: .5rem;
  display: flex;
}

.header-texts {
  align-items: center;
  margin-top: 1.5rem;
  display: flex;
}

.phone-header p {
  color: var(--Light-Grayish-Violet);
  opacity: .6;
  font-size: 1rem;
  font-weight: 300;
}

.phone-header h4 {
  color: var(--Light-Grayish-Violet);
  font-size: 1.5rem;
  font-weight: 300;
}

.phone-header h4, p {
  margin: .2rem 0 0;
}

.profile {
  background-color: #000;
  border: 1px solid #fff;
  border-radius: 50%;
  width: 30px;
  height: 30px;
  margin: 2rem 1rem 2rem 3.2rem;
}

.profile img {
  border-radius: 50%;
  width: 30px;
  height: 30px;
}

.dots-container {
  flex-direction: column;
  align-items: center;
  width: 6px;
  height: 18px;
  margin-left: 9rem;
  display: flex;
}

.dot {
  opacity: .8;
  background-color: #fff;
  border-radius: 50%;
  width: 3px;
  height: 3px;
  margin: .1rem;
}

.chevron {
  width: 19px;
  height: 20px;
  margin-left: .9rem;
  position: absolute;
  overflow: hidden;
}

.chevron:before, .chevron:after {
  content: "";
  background: #fff;
  border-radius: 20px;
  width: 9px;
  height: 3px;
  position: absolute;
  top: 50%;
}

.chevron:before {
  transform-origin: 103% 0;
  transform: rotate(114deg);
}

.chevron:after {
  transform-origin: 26% -68%;
  right: 0;
  transform: rotate(68deg);
}

.message-container {
  border-radius: 0 0 3.9rem 3.9rem;
  flex-direction: column;
  padding: .8rem .8rem 0;
  font-size: 11px;
  display: flex;
}

.message-left {
  color: var(--Moderate-Violet);
  transform-origin: 0 0;
  background-color: #ede5f1;
  border-radius: 15px 15px 15px 5px;
  width: 55%;
  height: -moz-fit-content;
  height: fit-content;
  margin: .5rem .5rem 1rem 0;
  padding: 1rem;
  font-weight: 450;
  transform: scale(.99);
}

.message-left p {
  margin: 0;
}

.dog-photo {
  align-self: end;
  margin-top: 1.5rem;
  margin-bottom: 1rem;
}

.dog-photo img {
  border-radius: 10px;
  width: 50px;
  height: 50px;
  margin: 0 .3rem;
}

.message-right {
  transform-origin: 100% 0;
  color: var(--Desaturated-Dark-Violet);
  background-color: #fff;
  border-radius: 15px 15px 0;
  align-self: end;
  width: -moz-fit-content;
  width: fit-content;
  height: -moz-fit-content;
  height: fit-content;
  margin: 0 0 .2rem;
  padding: 1rem;
  transform: scale(.93);
}

.message-one {
  width: 16rem;
}

.price h2 {
  margin-right: 2.5rem;
  font-size: 2rem;
  font-weight: 500;
}

.price {
  background: linear-gradient(90deg, var(--Light-Magenta) 10%, var(--Light-Violet) 100%);
  color: #fff;
  transform-origin: 0 0;
  border-radius: 15px 15px 15px 5px;
  justify-content: space-between;
  align-items: center;
  width: 92%;
  height: 5rem;
  margin-bottom: .1rem;
  display: flex;
  transform: scale(.8);
}

.price-container p, .white-border-circle {
  opacity: .7;
}

.price-container p {
  margin-right: 20px;
}

.white-border-circle {
  opacity: .5;
  border: 1px solid #fff;
  border-radius: 50%;
  width: 17px;
  height: 17px;
  margin-left: 1.5rem;
}

.price-txt-container {
  align-items: center;
  gap: 1rem;
  display: flex;
}

.input {
  border: 1px solid #fff;
  outline: 0;
  width: 90%;
  padding: 1.4rem;
}

input::placeholder {
  opacity: .5;
}

.input-container, .input {
  border-radius: var(--input-border-radius);
}

.input-container {
  background-color: #fff;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  margin-top: 1.5rem;
  display: flex;
}

.icon {
  margin-right: .5rem;
  font-size: 3rem;
}

.text-box {
  flex-direction: column;
  justify-content: center;
  max-width: 33%;
  margin-left: 13.5rem;
  line-height: 1.7;
  display: flex;
}

.text-box h1 {
  margin-bottom: 1rem;
  font-size: 3.2rem;
  font-weight: 500;
}

.text-box p {
  color: var(--Dark-Grayish-Violet);
}

@media screen and (width <= 769px) {
  body {
    flex-direction: column;
    width: 100%;
  }

  .phone-container {
    width: 40rem;
    right: 100vw;
  }

  .text-box {
    text-align: center;
    max-width: 30rem;
    margin: 5rem 0 0;
  }

  .phone {
    top: -40px;
    left: 25rem;
  }

  .phone-container {
    height: 70%;
    right: 24rem;
  }

  .right-box {
    display: none;
  }
}

.right-box {
  z-index: -3;
  background-color: var(--Light-Grayish-Violet);
  opacity: .4;
  border-radius: 35% 67% 0 0;
  width: 64.5rem;
  height: 100%;
  position: fixed;
  top: 8rem;
  right: -30rem;
}

